import React from 'react';
import { graphql, Link } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import Layout from '../../components/layout';

const About = ({ data }) => (
  <Layout
    page='about'
    subtitle='ジム情報'
    subtitleEn='INFORMATION'
    breadcrumbs={[{ link: '/about/', label: 'ジム情報' }]}
  >
    <main className='l-main'>
      <div className='wp-wrap-post'>
        <div data-row-pc='3' data-row-sp='1'>
          {[
            {
              link: '/about/studio/',
              image: data.Image01,
              label: 'OXWMについて'
            },
            {
              link: '/about/trainer/',
              image: data.Image02,
              label: 'トレーナー紹介'
            },
            {
              link: '/about/access/',
              image: data.Image03,
              label: 'アクセス'
            }
          ].map(({ link, image, label }) => (
            <Link key={link} to={link} className='c-panel c-panel--col'>
              <div className='c-panel__img'>
                <GatsbyImage
                  image={image.childImageSharp.gatsbyImageData}
                  alt={label}
                />
              </div>
              <div className='c-panel__detail'>
                <div className='c-panel__title'>{label}</div>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </main>
  </Layout>
);

export const query = graphql`
  query {
    Image01: file(relativePath: { eq: "thumbnail_studio-640x350.jpg" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    Image02: file(relativePath: { eq: "thumbnail_trainer-640x350.jpg" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    Image03: file(relativePath: { eq: "thumbnail_access-640x350.jpg" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
  }
`;

export default About;
